
@import "../../global.scss";

.projects{
  background-color: white;
  display: flex;
  flex-direction: column;
  align-items: center;
  

  .glow {

    font-size: 80px;
      background-image: url(https://fsb.zobj.net/crop.php?r=wgd4cvS6PxhKVIeSGOMkEL3RkmMDzkchVrmxMyrCqsitUWgcO71VH9a-tpuq0zInuTE4wYoZMjDVH8vH7O_KKU3WBFdGYtjzJ3VT1DvhFnLJ19_8xAHSAyUBVtcr-fx9WpjVRNRBN-fFyTKj);
      background-clip: text;
    color:transparent;
    text-align: center;
    animation: glow 1s ease-in-out infinite alternate;
    padding: 40px;

    animation: bganimation 10s infinite;

                    background-clip: text;

                    @keyframes bganimation {
                        0%{
                        background-position: 0% 50%;
                        }
                        50%{
                        background-position: 100% 50%;
                        }
                        100%{
                        background-position: 0% 50%;
                        }
                    }

  }
  
 


 


  ul{
    margin: 0;
    padding: 0;
    list-style: none;
    display: flex;
  

  li{
    font-size: 15px;
    margin: 50px;
    padding: 7px;
    border-radius: 13px;
    cursor: pointer;

    &.active{
      background-color:$primaryColor;
      color: white;
    }


  }
}
  .container{
      display: flex;
      flex-wrap:wrap ;
      

      .item{
            overflow: hidden;
        width: 600px;
        height: 400px;
        border-radius: 20px;
        border: 1px solid lightgray;
        margin: 10px 20px;
        display: flex;
        align-items: center;
        justify-content: center;
        color: white;
        position: relative;
        transition: all 0.6s ease;
        cursor: pointer;

        img{
       
          position: relative;
           object-fit:cover;
           z-index: 1;

           width: 600px;
           height: auto;
         } 
         &:hover{
           background-color:$mainColor;
           img{
             opacity: 0.2;
             z-index: 0;
           }
         }

        h3{
          position: absolute;
          font-size: 20px;
          display:flex;
          align-items: center;
          justify-content: center;
        }
      }
     
    
    }
}
  
