.app{
  height: 100vh;

  .sections{
    width: 100%;
    height: calc(100vh - 70px);
    background-color: green;
    position: relative;
    top:70px;
    scroll-behavior: smooth;
    scroll-snap-type: y mandatory;
    // scrollbar-width: none;//for firefox
    // &::-webkit-scrollbar{
    //   display: none;
    // }

    > *{

      width: 100vw;
      height: 100%;
      scroll-snap-align: start;

    }
  }
}

